import { SsidChartOutlined } from "@rp/react/web/mui/icons"
import "@tanstack/react-table"
import { companyAnalystRatings } from "../endpoints/companyAnalystRatings"
import { companyPriceTarget } from "../endpoints/companyPriceTarget"
import { companyQuote } from "../endpoints/companyQuote"
import { genericTable } from "../genericTable"
import { createRealTimeHeaderInfo } from "../utils/cacheTimeHelper"

const analysts = genericTable({
  tableName: "analysts",
  tableLabel: "Analysts",
  tableIcon: <SsidChartOutlined />,
  defaultVisible: [
    { endpoint: "companyQuote", columnKey: "price" },
    { endpoint: "companyPriceTarget", columnKey: "targetConsensus" },
    { endpoint: "companyPriceTarget", columnKey: "percentageDifference" },
    { endpoint: "companyAnalystRatings", columnKey: "consensus" },
    { endpoint: "companyAnalystRatings", columnKey: "totalAnalysts" },
  ],
  headerInfo: createRealTimeHeaderInfo("Analysts", "companyQuote"),
  columns: [
    companyQuote.price("analysts"),
    companyPriceTarget.targetConsensus("analysts"),
    companyPriceTarget.percentageDifference("analysts"),
    companyAnalystRatings.consensus("analysts"),
    companyAnalystRatings.totalAnalysts("analysts"),
    companyAnalystRatings.strongBuy("analysts"),
    companyAnalystRatings.buy("analysts"),
    companyAnalystRatings.hold("analysts"),
    companyAnalystRatings.sell("analysts"),
    companyAnalystRatings.strongSell("analysts"),
    companyPriceTarget.targetLow("analysts"),
    companyPriceTarget.targetHigh("analysts"),
  ],
})

export { analysts }
